.cardContainer {
	max-width: 600px;
	overflow: hidden;
	background-color: #484848;
	box-shadow: 0px 0px 15px -7px rgba(0, 0, 0, 0.94);
	border-radius: 10px;
	margin: 20px auto;
	border-color: #000;
	padding: 10px;
	display: flex;
	flex-direction: column;
	/* align-items: center; */
	justify-content: center;
}
.imageContainer {
	display: flex;
	align-self: center;
	max-height: 200px;
	overflow: hidden;
	border-radius: 10px;
	margin: 20px;
}
.descriptionContainer {
	max-width: 400px;
	overflow: hidden;
	margin: 20px;
}
